html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  font-size: 16px;
  line-height: 1.5;
  font-family: 'PT Sans', sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}



